@font-face {
  font-family: 'digital-italic';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format('truetype');
}

body {
  --text-color: rgb(0, 133, 0);
  --background-color: rgba(0, 75, 0, 0.3);
  --hover-color: rgba(255, 255, 255, 0.37);
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: rgb(44, 44, 44);
}

#title {
  font-family: 'digital-italic';
  color: var(--text-color);
  text-shadow:
    -2px -2px 1px var(--text-color),
    2px -2px 1px var(--text-color),
    -2px 2px 1px var(--text-color),
    2px 2px 1px var(--text-color);
  letter-spacing: 10px;
}

header,
footer {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

dialog {
  border-radius: 10px;
}

dialog::backdrop {
  background-color: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(12px);
}

.dialog-button-container {
  display: flex;
  justify-content: center;
  gap: 5%;
}

.mode-button {
  font-size: 24px;
  border-radius: 5px;
}

#single-input-container {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 20px;
}

#pvp-dialog-container,
#pvc-dialog-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

#dual-input-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
}

#pvp-start-button {
  width: 100%;
}

#title {
  font-size: 100px;
  text-align: center;
}

#gameboard-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

#ui-container {
  font-family: 'digital';
  color: var(--text-color);
  box-sizing: border-box;
  height: 100%;
  width: 20%;
  display: grid;
  grid-template-columns: 1;
  grid-template-rows: 1fr 1fr auto;
  gap: 20px;
  padding-left: 2%;
  padding-right: 2%;
}

#message-container,
#alert-container {
  border-radius: 10px;
  border: solid var(--text-color) 0.75px;
  background-color: var(--background-color);
  padding: 8px;
}

#message-container {
  text-align: center;
  font-size: 40px;
}

#alert-container {
  text-align: left;
  font-size: 48px;
}

#button-container {
  display: grid;
  place-items: center;
}

#turn-button {
  font-family: 'digital';
  color: var(--text-color);
  font-size: 40px;
  text-align: center;
  border-radius: 10px;
  border: solid var(--text-color) 0.75px;
  background-color: var(--background-color);
  padding: 8px;
}

#turn-button:hover {
  background-color: var(--hover-color);
}

.gameboard {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  gap: 2.5px;
}

.coordinate {
  height: 5vh;
  width: 5vh;
  border: solid var(--text-color) 0.75px;
  background-color: var(--background-color);
  display: grid;
  place-items: center;
  font-size: 30px;
  border-radius: 5px;
}

.active-coord:hover {
  background-color: var(--hover-color);
}

.ship {
  background-color: rgb(113, 197, 113);
}

.hit {
  background-color: rgb(255, 129, 129);
}

.miss {
  background-color: rgb(133, 133, 255);
}

#ship-info-container {
  display: flex;
  justify-content: space-around;
}

.ship-info {
  display: flex;
  justify-content: center;
  flex-direction: column;
  gap: 30px;
}

.ship-display {
  display: flex;
  gap: 1px;
}

.ship-unit {
  height: 30px;
  width: 30px;
  background-color: rgb(113, 197, 113);
  border: solid black 1px;
  border-radius: 3px;
}
